<script>
import Edu3D from './Edu3D.vue'
import AnimatedText from './AnimatedText.vue'
import PictureOfMe from './PictureOfMe.vue'
import MySkills from './MySkills.vue'

/* Author: Eduardo Alvarado 
* Date: 2023-02-14
* Description: Landing page component.
*/
export default {
  name: 'LandingPage',
  components: {
    Edu3D,
    AnimatedText,
    PictureOfMe,
    MySkills
  },
  data: function () {
    return {
      scroll: 0,
      scrollInCourse: null,
      scrollIsWaiting: true,
      socialNetworkIconsSize: 30,
    }
  },
  computed: {
    experienceYears(){
      // calculate years from 2016
      return new Date().getFullYear() - 2016;
    },
    yearsInLastWork(){
      // calculate years from 2017
      return new Date().getFullYear() - 2017;
    },
    aboutTitleArr(){
      return ['Over the', 'past {X} years,'.replace('{X}', this.experienceYears)];
    }
  },
  mounted(){
    this.$refs.edu3d.start();
    this.$nextTick(() => {
      this.scrollIsWaiting = false;
    });
  },
  methods:{
    listen(){
      window.addEventListener('scroll', this.handleScroll);
    },
    handleScroll(){
      this.scroll = window.scrollY;
    },
    show(){
      this.$refs.loader.style.opacity = 0;
      setTimeout(() => {
        this.$refs.loader.style.display = 'none';
      }, 400);

      this.listen();
      this.handleScroll();
    }
  }
}
</script>
